height: 100%;
}

.about-info{
  width: 60vw;
  color: #ffffff;
}

.about-section{
  height: 80vh;
  width: 100vw;
  position: absolute;
  top: 40vh;
  justify-content: center;
  align-items: end;
  display: flex;
  background-color: #ffffff;
}

.about-text{
  height: 60vh;
  width: 100vw;
  position: absolute;
  display: flex;
 flex-direction: column;
  justify-content: space-around;
  align-items: center;
  top: 80vh;
  background-color: #6E07F3;
  border-top:5px solid black ;
}

.about-title{
  position: absolute;
  top: 33vh;
  mix-blend-mode: difference;
  color: #ffffff;
  z-index: 1000;
  margin-left: 85%;
  transform: translateX(-50%);
  font-size: 5rem;
  width: 100%;
}




